// import logo from './logo.svg'
import { createBrowserRouter, Navigate, RouterProvider } from 'react-router-dom'
import FestiveTab from '../pages/home/festiveTab/festiveTab.component'
import GaneshaTab from '../pages/home/ganeshaTab/ganeshaTab.component'
import Home from '../pages/home/home.component'
import HomemadeTab from '../pages/home/homemadeTab/homemadeTab.component'
import OtherProductsTab from '../pages/home/otherProductsTab/otherProductsTab.component'
import RootRoutes from '../rootRoutes'
import './App.css'
import LanternsTab from '../pages/home/lanternsTab/lanternsTab.component'
import HomePage from '../pages/home/homePage/homePage.component'
import MainPage from '../pages/mainPage/mainPage.component'
import RecipesPage from '../pages/recipesPage/recipesPage.component'
import ChakliRecipe from '../components/recipes/chakliRecipe/chakliRecipe.component'

const router = createBrowserRouter([
  // { path: '/main', Component: Main },
  {
    path: '/',
    Component: MainPage,
    children: [
      { index: true, element: <Navigate to="/collection" replace /> },
      {
        path: 'collection',
        Component: Home,
        children: [
          { index: true, element: <Navigate to="products" replace /> },
          { path: 'homemade', Component: HomemadeTab },
          { path: 'products', Component: OtherProductsTab },
          { path: 'lanterns', Component: LanternsTab },
          // { path: 'ganesh', Component: GaneshaTab },
          { path: 'festive', Component: FestiveTab }
        ]
      },
      {
        path: 'recipes',
        Component: RecipesPage,
        children: [
          { index: true, element: <Navigate to="chakli" replace /> },
          { path: 'chakli', Component: ChakliRecipe }
        ]
      }
    ]
  },

  // {
  //   path: '/',
  //   Component: Home,
  //   children: [
  //     { path: '/', Component: OtherProductsTab },
  //     { path: 'homemade', Component: HomemadeTab },
  //     { path: 'products', Component: OtherProductsTab },
  //     { path: 'lanterns', Component: LanternsTab },
  //     // { path: 'ganesh', Component: GaneshaTab },
  //     { path: 'festive', Component: FestiveTab }
  //   ]
  // },

  { path: '*', Component: RootRoutes }
])

function App() {
  return <RouterProvider router={router}></RouterProvider>
}

export default App
