import React, { useRef } from 'react'
import { BoxedContainer, Container, RecipeContainer, RecipeImage } from './chakliRecipe.styles'
import { Image } from '@mui/icons-material'
import { Typography } from '@mui/material'
import LinksNavigation from '../../common/linksNavigation/linksNavigation.component'
import NavigationList from '../../common/navigationList/navigationList.component'

const ChakliRecipe = () => {
  const recipeRef = useRef(null)
  const tipsRef = useRef(null)
  const scrollToSection = ref => {
    ref.current?.scrollIntoView({ behavior: 'smooth' })
  }

  return (
    <Container>
      <NavigationList
        allTypes={[
          { name: 'Chakli Recipe', href: '#chakli-recipe', onClick: () => scrollToSection(recipeRef) },
          { name: 'Chakli Tips and tricks', href: '#chakli-tips', onClick: () => scrollToSection(tipsRef) }
        ]}
      />
      <RecipeContainer>
        <div ref={recipeRef} id="chakli-recipe">
          <RecipeImage src="/recipe_images/chakli/recipe.png" alt="Chakli" />
        </div>
        <div ref={tipsRef} id="chakli-tips">
          <RecipeImage src="/recipe_images/chakli/tips.png" alt="Chakli Tips" />
        </div>
      </RecipeContainer>
    </Container>
  )
}

export default ChakliRecipe
