import { SvgIcon } from '@mui/material'
import React, { useEffect } from 'react'
import { useMediaQuery } from 'react-responsive'
import { Outlet, useLocation, useNavigate, useParams } from 'react-router-dom'
import Header from '../../components/app/header/header.component'
import { MainLayout } from '../../components/common/mainLayout'
import TopNavigation from '../../components/common/navigationBar/topNavigation/topNavigation.component'
import {
  FESTIVE_ROUTE,
  GANESHA_ROUTE,
  HOMEMADE_ROUTE,
  LANTERNS_ROUTE,
  OTHER_PRODUCTS_ROUTE
} from '../../components/app/routerUtils'
function HomeIcon(props) {
  return (
    <SvgIcon {...props}>
      <path d="M10 20v-6h4v6h5v-8h3L12 3 2 12h3v8z" />
    </SvgIcon>
  )
}

const Home = () => {
  const { pathname } = useParams()
  useLocation()
  const location = useLocation()
  const navigate = useNavigate()
  // console.log('location.pathname', location.pathname)
  const defaultTab = location.pathname === '/' ? '2' : '1'
  const [currentTab, setCurrentTab] = React.useState(1)
  const isMobile = useMediaQuery({ query: `(max-width: 760px)` })

  useEffect(() => {
    console.log('useEffect', location.pathname)
    switch (location.pathname) {
      case HOMEMADE_ROUTE:
        setCurrentTab('1')
        break
      case OTHER_PRODUCTS_ROUTE:
        setCurrentTab('2')
        break
      case GANESHA_ROUTE:
        setCurrentTab('3')
        break
      case LANTERNS_ROUTE:
        setCurrentTab('5')
        break
      case FESTIVE_ROUTE:
        setCurrentTab('4')
        break
      default:
        setCurrentTab(defaultTab)
    }
  }, [location.pathname, currentTab, defaultTab])
  const changeTab = (event, newValue) => {
    console.log('changeTab', newValue)
    setCurrentTab(newValue)
    // console.log('newValue', newValue)
    switch (parseInt(newValue)) {
      case 1:
        navigate(HOMEMADE_ROUTE)
        break
      case 2:
        navigate(OTHER_PRODUCTS_ROUTE)
        break
      case 3:
        navigate(GANESHA_ROUTE)
        break
      case 4:
        navigate(FESTIVE_ROUTE)
        break
      case 5:
        navigate(LANTERNS_ROUTE)
        break
      default:
        navigate('/')
    }
  }

  // const colors = ['primary', 'danger', 'success', 'warning']
  // console.log(' currentTab', currentTab)
  return (
    <MainLayout>
      {/* <Header></Header> */}
      <TopNavigation currentTab={currentTab} changeTab={changeTab} />

      {/* <Divider orientation={'horizontal'} flexItem color="primary" /> */}
      <Outlet />
      {/* {isMobile && <BottomNavigation currentTab={currentTab} changeTab={changeTab} />} */}
    </MainLayout>
  )
}

export default Home
